import React, {useState}  from 'react'
import {Link} from 'react-router-dom'
import Logo from '../Logo'
import HamburgerMenu from 'react-hamburger-menu'

export default function Header() {
  const [open, setOpen] = useState(false)

  const handleClick = () => {
    setOpen(!open)
    if (!open) {
      document.getElementById("nav").style.height = "100%";
    } else {
      document.getElementById("nav").style.height = "0%";
    }
  }

  return (
    <div className="container">
    <header>
      <div className="logo">
        <Link to="/"><Logo /></Link>
      </div>
      <ul className="menu">
        <li>
          <Link to="/about">About</Link>
        </li>
        <li>
          <Link to="/learn">Learn</Link>
        </li>
        <li>
          <Link to="/get-started">Get Started</Link>
        </li>
        <li>
          <Link to="/dapps/minter" className="btn btn-primary">Minter</Link>
        </li>
        <li>
          <Link to="/get-grano" className="btn btn-danger">Get GRANO</Link>
        </li>
      </ul>
      <HamburgerMenu
        className={"hamburger-menu " + (open ? "open" : "closed") }
        isOpen={open}
        menuClicked={(e) => handleClick(e)}
        width={18}
        height={15}
        strokeWidth={1}
        rotate={0}
        color={'black'}
        borderRadius={0}
        animationDuration={0.5}
      />
      <div id="nav" className="overlay">
        <div className="overlay-content">
          <Link to="/about">About</Link>
          <Link to="/learn">Learn</Link>
          <Link to="/get-started">Get Started</Link>
          <Link to="/dapps/minter" className="btn btn-primary">Minter</Link>
          <Link to="/get-grano" className="btn btn-danger">Get GRANO</Link>
        </div>
      </div>
    </header>
    </div>
  )
}
