import React, {useState} from 'react'
import {ethers} from 'ethers'

let burnBtnLocked = false
const Burn = ({contractInstance, balance}) => {
  const [amount, setAmount] = useState('')
  const [period, setPeriod] = useState('')
  const [mintDate, setMintDate] = useState('')

  const addDays = (d) => {
    return ((new Date().getTime() / 1000) + d * 86400)
  }

  const commify = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
  }

  const getFormattedDate = t => {
    if (t) {
      const d = new Date(t*1000);
      return ('0' + d.getDate()).slice(-2) + '.' + ('0' + (d.getMonth()+1)).slice(-2) + '.' + d.getFullYear()
    }
    return '-'
  }

  const getMintedCount = () => {
    const quantity = period ** 2
    return (Math.floor(((quantity / 10000 * amount) + Number(amount)) * 100) / 100).toFixed(2)
  }

  const addMaxFunds = (max) => {
    setAmount(max);
  }

  const handleChange = (event) => {
    switch (event.target.name) {
      case 'amount':
        setAmount(event.target.value)  
        break
      case 'period':
        setPeriod(event.target.value)
        setMintDate(addDays(event.target.value))
        break
      default:
        break
    }
  }

  const burnNow = async() => {
    burnBtnLocked = true
    try {
      let tx = await contractInstance.burn(ethers.utils.parseEther(amount), period)
      tx.wait(1)
      .then(receipt => {
        console.log(receipt)
        burnBtnLocked = false
      })
    } catch (error) {
      console.log(error.message)
      burnBtnLocked = false
    }
  }

  return (
    <>
      <span className="name">Burn</span>
      <div className="flex">
        <div>
          <div className="field">
            <div>Amount</div>
            <div className="no-break">
              <input type="text" name="amount" value={amount} onChange={handleChange}  />
              <span>GRANO</span>
            </div>
            <div>
              <button type="button"className="max" onClick={() => addMaxFunds(balance)}>max</button>
            </div>
          </div>
          <div className="field mtb22">
            <div>Period</div>
            <div className="no-break">
              <input type="text" name="period" value={period} onChange={handleChange} />
              <span>DAYS</span>
            </div>
          </div>
          <button type="button" className="btn-burn lg" {...((burnBtnLocked) ? {'disabled': true} : '')} onClick={burnNow}>
            Burn Now
          </button>
        </div>
        <div className="field">
          <div className="fore-title">Amount to mint</div>
          <div className="fore-val">{getMintedCount() ? commify(getMintedCount()) + ' GRANO' : '-'}</div>
          <div className="fore-title">Rate</div>
          <div className="fore-val">{(getMintedCount() / amount * 100) ?  Math.floor(getMintedCount() / amount * 100) + '%' : '-'}</div>
          <div className="fore-title">Available on</div>
          <div className="fore-val">{getFormattedDate(mintDate)}</div>
        </div>
        <button type="button" className="btn-burn xs" {...((burnBtnLocked) ? {'disabled': true} : '')} onClick={burnNow}>
            Burn Now
          </button>
      </div>
    </>
  )

}

export default Burn