import React, {useEffect}  from 'react'
import {ethers} from 'ethers'
import DappHeader from '../DappHeader'
import Meta from '../Meta'

import ContractABI from '../../../contracts/ABI.json' 

export default function Connect({
  setLoading, setDefaultAccount, setTransactions, setBalance,
  contractInstance, provider, minter, getRecords
}) {

  useEffect(() => {
  }, [])

  const connect = () => {
    if (typeof window.ethereum !== 'undefined') {
      setLoading(true)
      window.ethereum.autoRefreshOnNetworkChange = false
      window.ethereum.request({ method: 'eth_requestAccounts' })
      .then((accounts) => {
        setDefaultAccount(accounts[0])
        if (!provider) provider = new ethers.providers.Web3Provider(window.ethereum)
        if (!contractInstance) contractInstance = new ethers.Contract(process.env.REACT_APP_CONTRACT_ADDRESS, ContractABI, provider.getSigner())
        contractInstance.balanceOf(accounts[0])
        .then((bal) => {
          if (minter) {
            setBalance(ethers.utils.formatEther(bal, {commify: true}))
            getRecords()
          } else {
            setBalance(ethers.utils.formatEther(bal))
          }
        }).catch(error => {
          console.log(error.message)
        })

        if (!minter) {
          const filtered = contractInstance.filters.Faucet(accounts[0])
          contractInstance.queryFilter(filtered)
          .then((transactions) => {
            if (transactions.length) {
              provider.getBlock(transactions[0].blockNumber).then((data) => {
                transactions[0].timestamp = data.timestamp
                setTransactions(transactions)
                setLoading(false)
              }).catch(error => {
                console.log(error.message)
              })
            } else {
              setLoading(false)
            }
          })
          .catch((error) => {
            console.log(error.message)
          })
        }
        
      })
      .catch((error) => {
        console.log(error.message)
      })

    }
  }

  return (
    <>
      <DappHeader/>
      <div className="bg bg-splash">
        <div className="dapp-container">
          <div className="connect-to">
            <h3>Connect your wallet</h3>
            <button type="button" onClick={connect}>
              <Meta/>
              <span>Metamask</span>
            </button>
          </div>
        </div>
      </div>
    </>
  )
}
