import React from 'react'
import ReactDOM from 'react-dom'
import { Router, Route, Switch } from 'react-router-dom'
import { createBrowserHistory } from 'history'
import ReactGA from 'react-ga'
import './index.css'
import Home from './components/Home'
import * as serviceWorker from './serviceWorker'

// import { I18nextProvider } from 'react-i18next'
// import i18n from './i18n'

import Minter from './components/Minter'
import PageNotFound from './components/PageNotFound'
import GetStarted from './components/GetStarted'
import GetGrano from './components/GetGrano'
import Faucet from './components/Faucet'
import About from './components/About'
import Learn from './components/Learn'
import Legal from './components/Legal'
import ScrollToTop from './components/common/ScrollToTop'

const history = createBrowserHistory()
ReactGA.initialize('UA-175648414-1');
history.listen((location, action) => {
    ReactGA.pageview(location.pathname + location.search);
});

ReactDOM.render(
  <React.StrictMode>
    {/* <I18nextProvider i18n={i18n}> */}
      <Router history={history}>
        <ScrollToTop />
        <Switch>
          <Route exact path="/" component={Home} />
          <Route exact path="/get-started" component={GetStarted} />
          <Route exact path="/get-grano" component={GetGrano} />
          <Route exact path="/dapps/faucet" component={Faucet} />
          <Route exact path="/about" component={About} />
          <Route exact path="/dapps/minter" component={Minter} />
          <Route exact path="/learn" component={Learn} />
          <Route exact path="/legal" component={Legal} />
          <Route path="*" component={PageNotFound} />
        </Switch>
      </Router>
    {/* </I18nextProvider> */}
  </React.StrictMode>,
  document.getElementById('root')
)

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister()
