import React, {useState, useEffect} from 'react'
import {ethers} from 'ethers'

import DappHeader from '../common/DappHeader'
import Account from '../common/Account'
import Connect from '../common/Connect'

import ContractABI from '../../contracts/ABI.json' 
import { Link } from 'react-router-dom'

let provider
let contractInstance
export default function Faucet() {
  const [defaultAccount, setDefaultAccount] = useState(null)
  const [balance, setBalance] = useState(null)
  const [transactions, setTransactions] = useState([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    document.title = 'Grano Protocol | Faucet'

    if (typeof window.ethereum !== 'undefined') {
      window.ethereum.autoRefreshOnNetworkChange = true
      window.ethereum.on('accountsChanged', function(accounts) {
        if (window.ethereum.selectedAddress) {
          setDefaultAccount(accounts[0])
          getBalance(accounts[0])
          getFilteredTransactions(accounts[0])
        }
        if (!window.ethereum.selectedAddress) {
          setDefaultAccount(null)
        }
      })
    }

    const handler = () => {
      window.ethereum.autoRefreshOnNetworkChange = true
      if (window.ethereum.selectedAddress) {
        setDefaultAccount(window.ethereum.selectedAddress)
        getBalance(window.ethereum.selectedAddress)
        getFilteredTransactions(window.ethereum.selectedAddress)
      }
    }

    if (document.readyState === "complete" && typeof window.ethereum !== 'undefined') {
      handler();
    } else {
      window.addEventListener('load', handler);
      return () => document.removeEventListener('load', handler);
    }
  }, [])

  const getBalance = (acc) => {
    if (!provider) provider = new ethers.providers.Web3Provider(window.ethereum)
    if (!contractInstance) contractInstance = new ethers.Contract(process.env.REACT_APP_CONTRACT_ADDRESS, ContractABI, provider.getSigner())
    contractInstance.balanceOf(acc)
    .then((bal) => {
      setBalance(ethers.utils.formatEther(bal))
    }).catch(error => {
      console.log(error.message)
    })
  }

  const getFilteredTransactions = (acc) => {
    if (!provider) provider = new ethers.providers.Web3Provider(window.ethereum)
    if (!contractInstance) contractInstance = new ethers.Contract(process.env.REACT_APP_CONTRACT_ADDRESS, ContractABI, provider.getSigner())
    const filtered = contractInstance.filters.Faucet(acc)
    contractInstance.queryFilter(filtered)
    .then((transactions) => {
      if (transactions.length) {
        provider.getBlock(transactions[0].blockNumber).then((data) => {
          transactions[0].timestamp = data.timestamp
          setTransactions(transactions)
          setLoading(false)
        }).catch(error => {
          console.log(error)
        })
      } else {
        setTransactions([])
        setLoading(false)
      }
    })
    .catch((error) => {
      console.log(error.message)
    })
  }

  const faucet = async() => {
    let tx = await contractInstance.faucet({from: window.ethereum.selectedAddress})
    tx.wait(1).then(receipt => {
      getBalance(defaultAccount)
      getFilteredTransactions(defaultAccount)
    })
  }

  const getFormattedDate = t => {
    const d = new Date(t*1000);
    return ('0' + d.getDate()).slice(-2) + '.' + ('0' + (d.getMonth()+1)).slice(-2) + '.' + d.getFullYear() + ' | ' + ('0' + d.getHours()).slice(-2) + ':' + ('0' + d.getSeconds()).slice(-2)
  }

  if (defaultAccount) {
    return (
      <>
        <DappHeader/>
        <div className="bg bg-faucet">
          <div className="dapp-container pt45 fau">
            <div className="account-info">
              <Account {...{balance}} />
            </div>

            <div className="block-item">
              <span className="name">Grano Faucet</span>
              <p>Each address can only once use Grano Faucet to get free GRANO.</p>
              { ((transactions.length) || loading)
                ? <button type="button" disabled>Get Grano</button>
                : <button type="button" onClick={faucet}>Get Grano</button>
              }
            </div>

            <div className="block-item">
              <span className="name">History</span>
              <div className="history">
                  <div>
                    <div className="head">Tx</div>
                    <div>{
                      (transactions.length) 
                        ? <Link to={{pathname: `${process.env.REACT_APP_LINK_TO_TRANSACTION}${transactions[0].transactionHash}`}} target="_blank" >View</Link>
                        : '-' }
                    </div>
                  </div>
                  <div>
                    <div className="head">Amount</div>
                    <div>{
                    (transactions.length) 
                        ? Number(ethers.utils.formatEther(transactions[0].args.tokens_amount)).toFixed(2)
                        : '-' }
                    </div>
                  </div>
                  <div>
                    <div className="head">Date</div>
                    <div>{
                    (transactions.length) 
                        ? getFormattedDate(transactions[0].timestamp)
                        : '-' }</div>
                  </div>
                </div>
            </div>
            <div style={{height: "20px"}}>
              &nbsp;
            </div>
          </div>
        </div>
      </>
    )
  }
  
  return <Connect 
    setLoading={setLoading}
    setDefaultAccount={setDefaultAccount}
    setTransactions={setTransactions}
    setBalance={setBalance}
    contractInstance={contractInstance}
    provider={provider}
    />
}
