import React from 'react'
import {ethers} from 'ethers'

import {Link} from 'react-router-dom'

const Record = ({contractInstance, tx, locked, setLocked}) => {

  const getFormattedDate = t => {
    if (t) {
      const d = new Date(t*1000);
      return ('0' + d.getDate()).slice(-2) + '.' + ('0' + (d.getMonth()+1)).slice(-2) + '.' + d.getFullYear() + ' | ' + ('0' + d.getHours()).slice(-2) + ':' + ('0' + d.getSeconds()).slice(-2)
    }
    return '-'
  }

  const commify = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
  }

  const getAmountToMint = (tx) => {
    const compensation = tx.args.lock_period.pow(2).div(ethers.BigNumber.from(10000)).mul(tx.args.tokens_amount)
    return commify((formatNumber(ethers.utils.formatEther(compensation.add(tx.args.tokens_amount)))).toFixed(2))
  }

  const getRemainingDays = (tx) => {
    //Must change from 10000 = 10sec to 86400 = day
    const days = ((parseInt((tx.args.burn_date * 1000 + tx.args.lock_period * 86400000), 10) - Date.now()) / 86400000).toFixed(0)
    return (days > 0) ? (tx.args.lock_period - (tx.args.lock_period - days)) : 0
  }

  const formatNumber = (n) => {
    return Math.round(n * 1e2) / 1e2
  }

  const getProgressCount = (tx) => {
    return parseInt(100 - (getRemainingDays(tx) / ethers.utils.formatUnits(tx.args.lock_period, 'wei') * 100), 10)
  }

  const handleMint = async(rec) => {
    setLocked(true)
    try {
      let tx = await contractInstance.mint(rec, {
        gasLimit: 750000
      })
      tx.wait(1)
      .then(receipt => {
        console.log(receipt)
        setLocked(false)
      })
    } catch (error) {
      console.log(error.message)
      setLocked(false)
    }
  }

  return (
    <>
      <div className="rec-item" key={tx.transactionHash}>
        <span>#{tx.rec + 1}</span>
        <div className="rec">
          <div className="rec-col">
            <div>Burn Date</div>
            <div><Link to={{pathname: `${process.env.REACT_APP_LINK_TO_TRANSACTION}${tx.transactionHash}`}} target="_blank">{getFormattedDate(tx.args.burn_date)}</Link></div>
          </div>
          <div className="rec-col">
            <div>Burn Amount</div>
            <div>{commify((formatNumber(ethers.utils.formatEther(tx.args.tokens_amount))).toFixed(2))} GRANO</div>
          </div>
          <div className="rec-col">
            <div>Period</div>
            <div>{Number(ethers.utils.formatUnits(tx.args.lock_period, 'wei'))} Days</div>
            
          </div>
          <div className="rec-col">
            <div>Amount To Mint</div>
            <div>{getAmountToMint(tx)} GRANO</div>
          </div>
        </div>
        <div className="rec">
          <div className="rec-col">
            <div>Mint Date</div>
            <div>
            {tx.minted
                ? <Link to={{pathname: `${process.env.REACT_APP_LINK_TO_TRANSACTION}${tx.mintedTx.transactionHash}`}} target="_blank">{getFormattedDate(tx.mintedTx.timestamp)}</Link>
                : '-'
              }
            </div>
          </div>
          <div className="rec-col">
            <div>Minted Amount</div>
            <div>
              {tx.minted
                ? commify((formatNumber(ethers.utils.formatEther(tx.mintedTx.args.tokens_amount))).toFixed(2)) + ` GRANO`
                : '-'
              }
            </div>
          </div>
          <div className="rec-col">
            <div>Progress</div>
            <div className="prog-flex">
              <div className="progress">
                <span style={{width: `${getProgressCount(tx)}%`}}></span>
                <i>{getProgressCount(tx)}%</i>
              </div>
              <span>{getRemainingDays(tx) ? getRemainingDays(tx) + ' Days left' : (tx.minted) ? '' : getRemainingDays(tx) + ' Days left'}</span>
            </div>
          </div>
          <div className="rec-col">
            <div>Status</div>
            <div className="status">
              {getRemainingDays(tx) > 0 && !tx.minted && 
                <button className="btn-disabled">
                  In progress
                </button>
              }
              {getRemainingDays(tx) === 0 && !tx.minted && 
                <button className="btn-mint" {...((locked) ? {'disabled': true} : '')} onClick={() => handleMint(tx.rec)}>
                  Mint
                </button>
              }
              {tx.minted && 
                <button className="btn-disabled">
                  Minted
                </button>
              }
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Record