import React, {useState, useEffect} from 'react'
import {ethers} from 'ethers'

import DappHeader from '../common/DappHeader'
import Account from '../common/Account'
import Connect from '../common/Connect'

import {useInterval} from '../utils'

import ContractABI from '../../contracts/ABI.json' 
import Burn from './Burn'
import Record from './Record'

let provider
let contractInstance
// let lastBlock
let recordsCount = -1
let displayedRecordsCount = 0
let limit = 5
let updateBy = 5
const Minter = () => {
  const [defaultAccount, setDefaultAccount] = useState(null)
  const [balance, setBalance] = useState(null)
  const [locked, setLocked] = useState(false)
  const [transactions, setTransactions] = useState([])
  const [updTransaction, setUpdTransaction] = useState({})
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    document.title = 'Grano Protocol | Minter'
    if (typeof window.ethereum !== 'undefined') {
      window.ethereum.autoRefreshOnNetworkChange = true
      window.ethereum.on('accountsChanged', function(accounts) {
        if (window.ethereum.selectedAddress) {
          setLoading(true)
          recordsCount = displayedRecordsCount = 0
          setTransactions([])
          setDefaultAccount(accounts[0])
          getBalance(accounts[0])
        }
        if (!window.ethereum.selectedAddress) {
          for (var i = 1; i < 9999; i++) {
            window.clearInterval(i);
          }
          recordsCount = displayedRecordsCount = 0
          setDefaultAccount(null)
          setTransactions([])
        }
      })
    }

    const handler = () => {
      window.ethereum.autoRefreshOnNetworkChange = true
      if (window.ethereum.selectedAddress) {
        setDefaultAccount(window.ethereum.selectedAddress)
        getBalance(window.ethereum.selectedAddress)
      }
    }
    if (document.readyState === "complete" && typeof window.ethereum !== 'undefined') {
      handler();
    } else {
      window.addEventListener('load', handler);
    }
    return () => {
      recordsCount = displayedRecordsCount = 0
      document.removeEventListener('load', handler)

    }
  }, [])

  useInterval(async() => {
    if (!provider) provider = new ethers.providers.Web3Provider(window.ethereum)
    if (!contractInstance) contractInstance = new ethers.Contract(process.env.REACT_APP_CONTRACT_ADDRESS, ContractABI, provider.getSigner())
    // const currentBlockNumber = await provider.getBlockNumber()
    // if (lastBlock !== await currentBlockNumber) {
      // lastBlock = currentBlockNumber
      // console.log(currentBlockNumber)

      const currentRecordsCount = parseInt(ethers.utils.formatUnits(await contractInstance.getRecordsCount(window.ethereum.selectedAddress), 'wei'), 10)
      if (recordsCount !== currentRecordsCount && (recordsCount === -1 || 0)) {
        recordsCount = currentRecordsCount
      }
      if (recordsCount !== currentRecordsCount && (recordsCount !== -1 || 0)) {
        setLoading(true)
        displayedRecordsCount = 0
        setTransactions([])
        recordsCount = currentRecordsCount
      }
      if (displayedRecordsCount === 0) {
        displayedRecordsCount = recordsCount
        getRecords()
      }
      getBalance(window.ethereum.selectedAddress)
      getUpdateRecords()
    // }
  }, 1000)

  useEffect(() => {
    if (transactions.length && updTransaction) {
      let copy = transactions
      const found = transactions.findIndex(tr => tr.transactionHash === updTransaction.transactionHash)
      if (found !== -1 && !locked) {
        copy[found] = updTransaction
        setTransactions(copy)
      }
    }
  }, [transactions, updTransaction, locked])

  const getBalance = (acc) => {
    if (!provider) provider = new ethers.providers.Web3Provider(window.ethereum)
    if (!contractInstance) contractInstance = new ethers.Contract(process.env.REACT_APP_CONTRACT_ADDRESS, ContractABI, provider.getSigner())
    contractInstance.balanceOf(acc)
    .then((bal) => {
      setBalance(ethers.utils.formatEther(bal, {commify: true}))
    }).catch(error => {
      console.log(error.message)
    })
  }

  const getRecord = async(i) => {
    let burnTx = await getBurnedTransactions(window.ethereum.selectedAddress, i-1)
    let mintTx = await getMintedTransactions(window.ethereum.selectedAddress, i-1)
    let rec = await contractInstance.getRecord(window.ethereum.selectedAddress, i-1)
    burnTx[0].minted = rec[3]
    burnTx[0].rec = i-1
    if (burnTx[0].minted && mintTx.length) {
      burnTx[0].mintedTx = mintTx[0]
      let block = await provider.getBlock(burnTx[0].mintedTx.blockNumber)
      burnTx[0].mintedTx.timestamp = block.timestamp
    }
    return burnTx[0]
  }

  const getRecords = async() => {
    if (displayedRecordsCount === 0) {
      displayedRecordsCount = recordsCount
    }
    for (let i = displayedRecordsCount; i > 0; i--) {
      if (displayedRecordsCount - i === limit) {
        displayedRecordsCount = i
        return
      }
      let tx = await getRecord(i)
      setTransactions(transactions => [...transactions, tx])
      setLoading(false)
    }
  }

  const getUpdateRecords = async() => {
    recordsCount = parseInt(ethers.utils.formatUnits(await contractInstance.getRecordsCount(window.ethereum.selectedAddress), 'wei'), 10)
    for (let i = recordsCount; i > 0; i--) {
      if (recordsCount - i === updateBy) return
      let tx = await getRecord(i)
      setUpdTransaction(tx)
    }
    setLoading(false)
  }

  const getBurnedTransactions = (acc, rec) => {
    if (!provider) provider = new ethers.providers.Web3Provider(window.ethereum)
    if (!contractInstance) contractInstance = new ethers.Contract(process.env.REACT_APP_CONTRACT_ADDRESS, ContractABI, provider.getSigner())
    const filtered = contractInstance.filters.Burn(acc, rec)
    return contractInstance.queryFilter(filtered)
  }

  const getMintedTransactions = (acc, rec) => {
    if (!provider) provider = new ethers.providers.Web3Provider(window.ethereum)
    if (!contractInstance) contractInstance = new ethers.Contract(process.env.REACT_APP_CONTRACT_ADDRESS, ContractABI, provider.getSigner())
    const filtered = contractInstance.filters.Mint(acc, rec)
    return contractInstance.queryFilter(filtered)
  }

  const loadMore = () => {
    setLoading(true)
    updateBy += limit
    getRecords()
  }

  if (defaultAccount) {
    return (
      <>
        <DappHeader/>
        <div className="bg">
          <div className="dapp-container pt45 minter">
            <div className="account-info">
              <Account {...{balance}} />
            </div>
            
            <div className="block-item">
              <Burn contractInstance={contractInstance} balance={balance}/>
            </div>

            <div className="block-item">
              <span className="name">Mint</span>

              {!recordsCount && !loading &&
                <p className="no-records">To start minting GRANO, you must burn some.</p>
              }

              {loading &&
                <p className="no-records">...</p>
              }

              {transactions.map((tx) => (
                <Record key={tx.transactionHash} contractInstance={contractInstance} tx={tx} locked={locked} setLocked={setLocked}/>
              ))}

              <div className="flex-center">
                { (transactions.length < recordsCount && !loading)
                  ? <button type="button" onClick={() => loadMore()} className="load-more">Load More</button>
                  : ''
                }
              </div>
            </div>
            <div style={{height: "20px"}}>
              &nbsp;
            </div>

          </div>
        </div>
      </>
    )
  }

  return <Connect 
    minter={true}
    getRecords={getRecords}
    setLoading={setLoading}
    setDefaultAccount={setDefaultAccount}
    setTransactions={setTransactions}
    setBalance={setBalance}
    contractInstance={contractInstance}
    provider={provider}
    />

}

export default Minter