import React  from 'react'
import Jazzicon, { jsNumberForAddress } from 'react-jazzicon'
import Icon from '../Icon'

export default function Account({balance}) {
  const shortAddress = () => {
    return window.ethereum.selectedAddress.substring(0,5) + "..." + window.ethereum.selectedAddress.substr(window.ethereum.selectedAddress.length - 6);
  }

  const commify = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
  }

  const formatNumber = (n) => {
    return (Math.round(n * 1e2) / 1e2).toFixed(2)
  }

  return (
    <div className="account rows">
      <div className="col">
        <div>
          <Icon color="#212121" width="60" height="60" />
        </div>
        <div className="balance-info">
          <div>Balance</div>
          <div><strong>{(balance) ? commify(formatNumber(balance)) : '0.00'}</strong> GRANO</div>
        </div>
      </div>
      <div className="col">
        <div>
          <Jazzicon diameter={60} seed={jsNumberForAddress(window.ethereum.selectedAddress)} />
        </div>
        <div className="address-info">
          <div>{shortAddress()}</div>
          <div className="flex-start">
            connected
          </div>
        </div>
      </div>
    </div>
  )
}
